"use client"

import React, { useEffect, useRef, useState } from 'react';
import Phaser from 'phaser';

const IsekaiVillageGame: React.FC = () => {
  const gameRef = useRef<HTMLDivElement>(null);
  const gameInstanceRef = useRef<Phaser.Game | null>(null);
  const [isAssetsLoaded, setIsAssetsLoaded] = useState(false);
  const [phaserLoaded, setPhaserLoaded] = useState(false);

   useEffect(() => {
    if (typeof window !== "undefined") {
      import("phaser").then((Phaser) => {
        const config = {
          type: Phaser.AUTO,
          width: 800,
          height: 600,
          physics: {
            default: "arcade",
            arcade: { gravity: { y: 0 } },
          },
          scene: {
            preload: function () {
              this.load.image("player", "/assets/player.png"); // Load character sprite
            },
            create: function () {
              this.add.image(400, 300, "player"); // Add character at center
            },
            update: function () {
              // Movement logic will go here
            },
          },
        };

        if (!gameRef.current) {
          gameRef.current = new Phaser.Game(config);
          setPhaserLoaded(true);
        }
      });
    }
  }, []);


  useEffect(() => {
    if (typeof window !== 'undefined' && isAssetsLoaded && gameRef.current) {
      class IsekaiVillageScene extends Phaser.Scene {
        private player!: Phaser.Physics.Arcade.Sprite;
        private npcs!: Phaser.Physics.Arcade.Group;
        private cursors!: Phaser.Types.Input.Keyboard.CursorKeys;
        private background!: Phaser.GameObjects.Image;

        constructor() {
          super({ key: 'IsekaiVillageScene' });
        }

        preload() {
          // Load background
          this.load.image('background', '/tavern.jpeg');

          this.load.spritesheet('player', '/tavern.jpeg', {
            frameWidth: 64,  
            frameHeight: 64
          });

          this.load.spritesheet('npc', '/tavern.jpeg', {
            frameWidth: 64, 
            frameHeight: 64
          });
        }

        create() {
          // Add background
          this.background = this.add.image(400, 300, 'background')
            .setDisplaySize(800, 600);

          // Create player animations
          this.createPlayerAnimations();

          // Create NPC animations
          this.createNPCAnimations();

          // Create player
          this.createPlayer();

          // Create NPCs
          this.createNPCs();

          // Setup controls
          this.setupControls();

          // Add debug text
          this.add.text(10, 10, 'Use arrow keys to move', { 
            color: '#ffffff', 
            backgroundColor: '#000000',
            padding: "10px"
          });
        }

        createPlayerAnimations() {
          // Walking animations for different directions
          this.anims.create({
            key: 'player-walk-down',
            frames: this.anims.generateFrameNumbers('player', { start: 0, end: 3 }),
            frameRate: 10,
            repeat: -1
          });

          this.anims.create({
            key: 'player-walk-left',
            frames: this.anims.generateFrameNumbers('player', { start: 4, end: 7 }),
            frameRate: 10,
            repeat: -1
          });

          this.anims.create({
            key: 'player-walk-right',
            frames: this.anims.generateFrameNumbers('player', { start: 8, end: 11 }),
            frameRate: 10,
            repeat: -1
          });

          this.anims.create({
            key: 'player-walk-up',
            frames: this.anims.generateFrameNumbers('player', { start: 12, end: 15 }),
            frameRate: 10,
            repeat: -1
          });
        }

        createNPCAnimations() {
          // NPC walking animations
          this.anims.create({
            key: 'npc-walk-down',
            frames: this.anims.generateFrameNumbers('npc', { start: 0, end: 3 }),
            frameRate: 8,
            repeat: -1
          });
        }

        createPlayer() {
          // Create player sprite
          this.player = this.physics.add.sprite(400, 300, 'player')
            .setScale(1.5)
            .setCollideWorldBounds(true);
        }

        createNPCs() {
          // Create NPC group
          this.npcs = this.physics.add.group();

          // Create multiple NPCs with random movement
          const npcPositions = [
            { x: 200, y: 200 },
            { x: 600, y: 400 },
            { x: 300, y: 500 }
          ];

          npcPositions.forEach(pos => {
            const npc = this.npcs.create(pos.x, pos.y, 'npc')
              .setScale(1.5)
              .setCollideWorldBounds(true);
            
            // Random movement for NPCs
            this.time.addEvent({
              delay: Phaser.Math.Between(2000, 5000),
              callback: () => this.moveNPC(npc),
              loop: true
            });
          });
        }

        moveNPC(npc: Phaser.Physics.Arcade.Sprite) {
          const directions = [
            { x: -1, y: 0 },  // Left
            { x: 1, y: 0 },   // Right
            { x: 0, y: -1 },  // Up
            { x: 0, y: 1 }    // Down
          ];

          const randomDir = Phaser.Math.RND.pick(directions);
          const speed = 100;

          npc.setVelocity(randomDir.x * speed, randomDir.y * speed);
          npc.anims.play('npc-walk-down', true);

          // Stop movement after a short time
          this.time.delayedCall(1000, () => {
            npc.setVelocity(0);
          });
        }

        setupControls() {
          // Keyboard controls
          this.cursors = this.input.keyboard.createCursorKeys();
        }

        update() {
          const speed = 200;
          this.player.setVelocity(0);

          // Player movement and animation
          if (this.cursors.left.isDown) {
            this.player.setVelocityX(-speed);
            this.player.anims.play('player-walk-left', true);
          }
          else if (this.cursors.right.isDown) {
            this.player.setVelocityX(speed);
            this.player.anims.play('player-walk-right', true);
          }

          if (this.cursors.up.isDown) {
            this.player.setVelocityY(-speed);
            this.player.anims.play('player-walk-up', true);
          }
          else if (this.cursors.down.isDown) {
            this.player.setVelocityY(speed);
            this.player.anims.play('player-walk-down', true);
          }
        }
      }

      // Game configuration
      const config: Phaser.Types.Core.GameConfig = {
        type: Phaser.AUTO,
        parent: gameRef.current,
        width: 800,
        height: 600,
        backgroundColor: '#000000',
        physics: {
          default: 'arcade',
          arcade: {
            gravity: {
              y: 0,
              x: 0
            },
            debug: false
          }
        },
        scene: [IsekaiVillageScene]
      };

      // Create game instance
      try {
        gameInstanceRef.current = new Phaser.Game(config);
      } catch (error) {
        console.error('Failed to initialize Phaser game:', error);
      }

      // Cleanup on unmount
      return () => {
        gameInstanceRef.current?.destroy(true);
      };
    }
  }, [isAssetsLoaded]);

  return (
    <div 
      ref={gameRef} 
      className="w-full h-[600px] border-2 border-gray-300 rounded-lg"
    />
  );
};

export default IsekaiVillageGame;